* {
      font-family: sans-serif;
      color: #333;
      margin: 0;
    }
    .addbookform label{
      width: 100px;
      display: inline-block;
    }
    
    .addbookform div{
      margin: 2px 3px;
    }    
    .addbookform input,
    form input{
      border: 1px solid #e5e5e5;
      margin: 2px;
      outline: 0
    }

    .btn{
    cursor: pointer;
    display: inline-block;
    font-weight: 400;
    text-align: center;
    white-space: nowrap;
    vertical-align: middle;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    border: 1px solid transparent;
    padding: .1rem .2rem;
    font-size: 1rem;
    line-height: 1.5;
    border-radius: .25rem;
      text-decoration: none;
    }
    .btn-success:hover {
    color: #fff;
    background-color: #28a745;
    border-color: #28a745;
    }
    .btn-success{
    color: #fff;
    background-color: #28a745;
    border-color: #28a745;
    }
    .btn-select{
      background-color: #0f3f1b;
      border-color: #0a3815;
    }
   .night{
     background: #444;   
     padding: 10px;
     color: #efefef;
   }
   .day{
     background: #efefef;    
     color: #868686;
   }
   .book-selected{
     background: rgba(130,160,180,.2);    
   }
   .basket{
   position: fixed;
   background: rgba(255,255,255,.8);
   top:150px;
   right: 0;
   margin: 20px;
   border: 1px solid rgba(0,0,0,.1);
  }
  .basket-item, .basket h3{
   padding: 5px 10px;
  }
  .basket-item span,
  .basket-item a {
   padding: 0 7px
  }
  .pagination{
    list-style: none;
    display: flex;
  }
  .page-noselect, .page-select{
    color: #fff;
    background: #28a745;
    margin: 5px;
    cursor: pointer;
    border: solid 3px #28a745;
    border-radius: 5px;
    font-size: 1.5rem;
    font-weight: bold;
  }
  .page-select{
    background: #0f3f1b;
    border-color: #0f3f1b;
  }
  .check-container{
    display: flex;
    margin: 10px;
    font-size: 1.5rem;
  }
  .check{
      color: rgb(138, 138, 138);
      width: 45px;
      height: 45px;
      cursor: pointer;
      font-size: 2.5rem;
  }
  .checked{
      color: #28a745;
  }
  .navbar{
    background-color: #28a745;
    min-height: 40px;
    position: sticky;
    top: 0;
  }
  .navbar button{
    background-color: #28a745;
    color: #fff;
    font-size: 1.5rem;
    font-weight: bold;
    position:fixed;
    right: 0px; 
    height: 40px;
    cursor: pointer;
    outline: none;
    border: none;
    border-left: 1px solid #fff;
    width: 260;
  }
  .navbar button:hover{
    background-color: #0f3f1b;
  }
  .navbar ul{
    position:fixed;
    right: 0px;
    width: 244px;
    top: 40px;
    padding: 0px;
    font-size: 1.2rem;
    font-weight: bold;
    padding: 5px;
    background-color: #28a745;
  }
  .navbar li{
    background-color: #28a745;
    list-style-type: none;
    padding: 5px;
  }
  .navbar li:hover{
    background-color: #0f3f1b;
  }
  .navbar a{
    text-decoration: none;
    color: #fff;
  }
